@import 'variables', 'nav', 'Home', 'fancy-button';

* {
  margin: 0;
  padding: 0;
  text-decoration: none; }

body {
  font-family: 'Lato', sans-serif; }

.background-img {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    filter: brightness(0.5);
    sheight: 100vh; } }
.container {
  background-color: #fff;
  min-height: 200px;
  margin-top: 100vh;
  position: absolute;
  width: 100%;
  z-index: -1; }
.footer {
  background-color: black;
  color: pink;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  margin-top: 50px; }

